import * as React from "react";
import { graphql, type HeadFC, type PageProps } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Company } from "../interfaces";
import data from "../data/data.json";
import Layout from "./layout";
import images from "../images";
import Carousel from "../components/carousel";
import ServicesTiles from "../components/servicesTiles";

const info: Company = data[0];

const IndexPage: React.FC<PageProps> = () => {
  const { t } = useTranslation();

  const carouselImages = [
    { src: images.carousel_1.src, alt: images.carousel_1.alt },
    { src: images.carousel_2.src, alt: images.carousel_2.alt },
    { src: images.carousel_3.src, alt: images.carousel_3.alt },
  ];

  const textOverlay = [
    { content: t("carousel.text_1"), positionClass: styles.slide_1 },
    { content: t("carousel.text_2"), positionClass: styles.slide_2 },
    { content: t("carousel.text_3"), positionClass: styles.slide_3 },
  ];
  return (
    <>
      <Layout data={info}>
        <section
          id={info.menu[0]}
          className="flex w-full mx-auto bg-primary/25 pt-2 sm:pt-16 pb-2 sm:pb-44"
        >
          <Carousel
            images={carouselImages}
            textOverlay={textOverlay}
            speed={6000}
          />
        </section>

        <section id={info.menu[1]} className="relative top-4 sm:-top-28">
          <ServicesTiles />
        </section>

        <section id={info.menu[2]} className={styles.whoIam}>
          <div className="flex flex-col">
            <h2 className="text-3xl font-bold text-center">
              {t("whoIam.title")}
            </h2>
            <p className="text-center text-xl italic">{t("whoIam.subTitle")}</p>
          </div>
          <div className="flex">
            <div className="w-1/4 pt-8">
              <img src={images.ana_1.src} alt={images.ana_1.alt} />
            </div>
            <div  className="w-3/4 p-8">
              <p>{t("whoIam.copy_1")}</p>
              <p>{t("whoIam.copy_2")}</p>
            </div>
          </div>
          <div className="flex">
            <div className="w-3/4 text-right p-8">
              <p>{t("whoIam.copy_3")}</p>
              <p>{t("whoIam.copy_4")}</p>
            </div>
            <div className="w-1/4">
              <img src={images.ana_2.src} alt={images.ana_2.alt} />
            </div>
          </div>
        </section>
      </Layout>
    </>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>{info.companyName}</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  slide_1: "right-0",
  slide_2: "right-0",
  slide_3: "right-0",
  whoIam: "text-secondary max-w-[1024px] mx-auto",
};
